/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
	color: #181518;
	font-family: "Montserrat", sans-serif;
	line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
	font-family: "Montserrat", sans-serif;
	margin: unset;
}

h1 {
	font-size: 1.5rem;
}

@media screen and (min-width: 1600px) {
	h1 {
		font-size: 2rem;
	}
}

h2 {
	font-size: 1.1rem;
}

h3 {
	font-size: 1rem;
}

h4 {
	font-size: 1rem;
}

h5 {
	font-size: 1em;
}

.excerpt-title {
	font-size: 1.8rem;
}

.search-title {
	font-size: 2rem;
}

p {
	margin-bottom: 1.8rem;
	font-family: "Crimson Text", serif;
	font-size: 1rem;
	margin: 1rem 0;
}

@media screen and (min-width: 1600px) {
	p {
		font-size: 1.2rem;
	}
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	font-family: "Courier 10 Pitch", courier, monospace;
	font-size: 0.9375rem;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 0.9375rem;
}

abbr,
acronym {
	border-bottom: 1px dotted #CC1F38;
	cursor: help;
}

mark,
ins {
	text-decoration: none;
}

big {
	font-size: 125%;
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
/* Inherit box-sizing to more easily change it's value on a component level.
@link http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
	overflow-x: hidden;
}

body {
	background: white;
	overflow-x: hidden;
}

hr {
	background-color: #F7F7F8;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul,
ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 1.5em 1.5em;
}

img {
	object-fit: cover;
	margin-top: 1.2em;
	max-height: 300px;
	max-width: 100%;
}

@media screen and (min-width: 1024px) {
	img {
		max-height: 800px;
	}
}

.wp-block-image {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.wp-block-image a {
	display: flex;
}

.wp-block-cover {
	max-width: initial;
	max-height: 300px;
}

figure {
	margin: 1em 0;
}

.grecaptcha-badge {
	opacity: 0;
	visibility: hidden;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
.site-header {
	z-index: 10000000;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	height: 60px;
	width: 100%;
	top: 0;
	background-color: white;
	transition: all 0.2s ease-out;
	position: fixed;
}

@media screen and (min-width: 1024px) {
	.site-header {
		justify-content: space-around;
		height: 100px;
	}
}

.smaller-header {
	box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2);
}

.smaller-header img {
	width: 40px;
	height: 40px;
}

@media screen and (min-width: 576px) {
	.smaller-header {
		height: 60px;
	}
	.smaller-header img {
		width: 50px;
		height: 50px;
	}
}

@media screen and (min-width: 1024px) {
	.smaller-header {
		height: 80px;
	}
	.smaller-header img {
		width: 60px;
		height: 60px;
	}
	.smaller-header p {
		font-size: 0.8rem;
	}
}

@media screen and (max-width: 576px) {
	.smaller-header {
		height: 50px;
	}
}

.site-branding {
	display: flex;
	align-content: center;
	flex-wrap: wrap;
}

.site-description,
.site-title,
.main-navigation {
	margin: auto 1rem;
}

.site-description {
	transition: all 0.2s ease-in;
	font-size: 0.7rem;
}

@media screen and (max-width: 558px) {
	.site-description {
		display: none;
	}
}

@media screen and (min-width: 768px) {
	.site-description {
		font-size: 0.9rem;
	}
}

.site-title {
	margin: auto 1rem;
	font-size: unset;
}

.site-title a {
	display: inline;
	font-size: unset;
	font-family: "Montserrat", sans-serif;
	font-weight: 400;
	text-transform: uppercase;
	color: #181518;
	font-size: 0.8rem;
	line-height: unset;
}

@media screen and (max-width: 335px) {
	.site-title a {
		display: none;
	}
}

@media screen and (min-width: 576px) {
	.site-title a {
		font-size: 1.2rem;
	}
}

.site-title a:visited {
	color: #181518;
}

.site-title a:hover {
	color: #181518;
}

@media screen and (min-width: 1600px) {
	.smaller-navbar a {
		font-size: 1.1rem;
	}
}

.custom-logo {
	height: 50px;
	width: 50px;
	margin-top: 0.5em;
	margin-left: 10px;
	transition: height 0.2s ease-out, width 0.2s ease-out;
}

@media screen and (min-width: 1024px) {
	.custom-logo {
		height: 70px;
		width: 70px;
	}
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: none;
	background: #CC1F38;
	/* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #CC1F38, #AC022C);
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #CC1F38, #AC022C);
	color: #F7F7F8;
	cursor: pointer;
	font-size: 1rem;
	border-radius: 30px;
	padding: 12px 30px;
}

@media screen and (min-width: 1024px) {
	button,
	input[type="button"],
	input[type="reset"],
	input[type="submit"] {
		box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2);
		transition: all 0.2s ease-in;
	}
}

@media screen and (min-width: 1024px) {
	button:hover,
	input[type="button"]:hover,
	input[type="reset"]:hover,
	input[type="submit"]:hover {
		box-shadow: 0 2px 4px #07080a;
	}
}

.read-more {
	background: unset;
	color: #CC1F38;
	height: unset;
	width: unset;
	margin-bottom: 1rem;
	border: unset;
	box-shadow: unset;
	font-size: 1rem;
	transition: unset;
	padding: unset;
	font-family: "Crimson Text", serif;
}

@media screen and (min-width: 1280px) {
	.read-more {
		margin-top: 1rem;
	}
}

@media screen and (min-width: 1024px) {
	.read-more {
		border-radius: 30px;
	}
}

@media screen and (min-width: 1600px) {
	.read-more {
		font-size: 1.2rem;
	}
}

.read-more:hover,
.read-more:active {
	box-shadow: unset;
}

@media screen and (min-width: 1024px) {
	.card:first-of-type .read-more {
		display: block;
		color: #AC022C;
		background: #F7F7F8;
		border: 1px solid #AC022C;
		cursor: pointer;
		margin-top: 1rem;
		box-shadow: none;
		font-family: "Crimson Text", serif;
		transition: all 0.2s ease;
		margin-bottom: 1rem;
		padding: 12px 30px;
	}
}

@media screen and (min-width: 1280px) {
	.card:first-of-type .read-more {
		margin-top: 1rem;
	}
}

@media screen and (min-width: 1024px) {
	.card:first-of-type .read-more {
		padding-left: 30px;
	}
}

.card:first-of-type .read-more:active {
	transform: scale(0.99);
}

@media screen and (min-width: 1024px) {
	.card:first-of-type .read-more:hover {
		background: #CC1F38;
		/* fallback for old browsers */
		background: -webkit-linear-gradient(to right, #CC1F38, #AC022C);
		/* Chrome 10-25, Safari 5.1-6 */
		background: linear-gradient(to right, #CC1F38, #AC022C);
		color: #F7F7F8;
		box-shadow: unset;
	}
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	color: #CC1F38;
	border: 1px solid #CC1F38;
	border-radius: 3px;
	padding: 3px;
	width: 100%;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
	color: #7B6A7F;
}

select {
	border: 1px solid #CC1F38;
}

textarea {
	width: 100%;
	padding: 1rem;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
	color: #CC1F38;
	text-decoration: none;
	transition: color 0.3s ease-out;
	transition: color 0.3s ease-out;
	transition: color 0.3s ease-out;
}

a:visited {
	color: #CC1F38;
}

a:hover, a:focus, a:active {
	color: #AC022C;
}

a:focus {
	outline: thin dotted;
}

a:hover, a:active {
	outline: 0;
}

.nav-previous p,
.nav-next p {
	color: #CC1F38;
	font-size: 0.8em;
}

.nav-title {
	font-family: "Crimson Text", serif;
}

.nav-links {
	display: flex;
	justify-content: center;
	line-height: 1rem;
}

.nav-links .page-numbers {
	display: block;
	padding: 0.5rem 0.8rem;
}

.nav-links .current {
	border: 1px solid #CC1F38;
	color: #CC1F38;
	border-radius: 5px;
}

.cat-links a {
	text-transform: uppercase;
	color: #7B6A7F;
	font-size: 0.7rem;
	line-height: 1;
}

@media screen and (min-width: 1024px) {
	.cat-links a {
		font-size: 0.9rem;
	}
}

.cat-links a:hover {
	color: #CC1F38;
	text-decoration: underline;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
	padding: 1rem;
}

.tags-links {
	font-size: 0.9rem;
	font-family: "Montserrat", sans-serif;
	display: block;
	margin: auto;
	text-align: center;
	margin-top: 50px;
}

.edit-link {
	font-size: 0.8  rem;
	text-transform: uppercase;
}

.pagination {
	padding: 2rem;
}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.menu {
	color: #181518;
	margin: 0;
	padding-left: 0;
	list-style-type: none;
	letter-spacing: 0.1rem;
	display: flex;
	flex-direction: column;
}

@media screen and (min-width: 1600px) {
	.menu {
		flex-direction: row;
		height: unset;
		position: static;
		background-color: unset;
		justify-content: flex-end;
		min-width: 500px;
	}
}

.menu li {
	position: relative;
	padding: 1rem 0;
}

@media screen and (min-width: 1600px) {
	.menu li:hover > a,
	.menu li.focus > a {
		border-bottom: 1px solid #CC1F38;
		color: #CC1F38;
	}
}

.menu li a {
	display: block;
	color: #F7F7F8;
	font-size: 14px;
}

@media screen and (min-width: 1600px) {
	.menu li a {
		color: #181518;
		box-sizing: border-box;
		border-bottom: 1px solid white;
		border-width: 1;
		padding: 0.4em 1em;
		border-width: 1px;
		transition: border 0.3s ease-in;
	}
}

.main-menu {
	position: absolute;
	left: 0px;
	top: 0px;
	display: flex;
	flex-direction: column;
	height: 100vh;
	width: 250px;
	background: -webkit-linear-gradient(#CC1F38, #AC022C);
	justify-content: center;
	align-items: center;
	transform: translateX(-350px);
	box-shadow: 0 1px 5px rgba(0, 0, 0, 0.5);
	transition: transform 0.2s ease-in;
}

@media screen and (min-width: 1600px) {
	.main-menu {
		box-shadow: unset;
		position: static;
		flex-direction: row;
		height: unset;
		width: unset;
		background: unset;
		min-width: 350px;
		transition: unset;
		transform: translateX(0px);
		top: 0;
	}
}

.menu-active {
	display: flex;
	transform: translateX(0px);
}

@media screen and (min-width: 1600px) {
	.menu-active {
		position: unset;
	}
}

.posts-navigation,
.post-navigation {
	max-width: 900px;
	margin: 3em auto;
}

.posts-navigation .nav-links,
.post-navigation .nav-links {
	display: flex;
	margin: 0 auto;
	font-size: 1rem;
}

@media screen and (min-width: 1024px) {
	.posts-navigation .nav-links,
	.post-navigation .nav-links {
		font-size: 1.2rem;
	}
}

@media screen and (min-width: 1600px) {
	.posts-navigation .nav-links,
	.post-navigation .nav-links {
		font-size: 1.3rem;
	}
}

.toggler {
	padding: auto 10px;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	margin: 5px 5px;
	cursor: pointer;
}

@media screen and (min-width: 1600px) {
	.toggler {
		display: none;
	}
}

.toggler div {
	height: 2px;
	width: 30px;
	margin: 4px 0;
	background-color: #181518;
}

@media screen and (max-width: 1024px) {
	.toggler div {
		width: 23px;
		margin: 3px 0;
	}
}

.toggler div:nth-of-type(2) {
	width: 15px;
}

.toggler div:nth-of-type(3) {
	width: 10px;
}

.nav-menu {
	display: flex;
}

.sub-menu {
	display: none;
}

.menu-hider {
	z-index: -10000000000;
	display: none;
}

.menu-hider-active {
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
}

/**
*Menu stagered animnation 
*/
.open-menu-items li {
	animation: 0.4s ease pulse;
	animation-fill-mode: both;
}

.open-menu-items li:nth-child(1) {
	animation-delay: 0.06667s;
}

.open-menu-items li:nth-child(2) {
	animation-delay: 0.13333s;
}

.open-menu-items li:nth-child(3) {
	animation-delay: 0.2s;
}

.open-menu-items li:nth-child(4) {
	animation-delay: 0.26667s;
}

.open-menu-items li:nth-child(5) {
	animation-delay: 0.33333s;
}

.open-menu-items li:nth-child(6) {
	animation-delay: 0.4s;
}

.open-menu-items li:nth-child(7) {
	animation-delay: 0.46667s;
}

.open-menu-items li:nth-child(8) {
	animation-delay: 0.53333s;
}

.open-menu-items li:nth-child(9) {
	animation-delay: 0.6s;
}

@keyframes pulse {
	0% {
		transform: translateX(-75px);
	}
	100% {
		transform: translateX(0px);
	}
}

