}

iframe {
	margin: 1rem 0;
}

/* Make sure logo link wraps around logo image. */
/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption,
figcaption {
	margin: 1rem 0;
	max-width: 100%;
	font-size: 0.8rem;
	font-weight: lighter;
	font-style: italic;
	text-align: center;
}

.wp-caption img[class*="wp-image-"],
figcaption img[class*="wp-image-"] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.wp-caption .wp-caption-text,
figcaption .wp-caption-text {
	margin: 0 0;
}

.aligncenter {
	margin-bottom: 0;
}

.aligncenter figcaption {
	margin: 1rem 0;
}

.wp-caption-text {
	text-align: center;
}

/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
.site-footer {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 2rem 0;
	background-color: #101010;
	color: white;
}

.social-media {
	display: flex;
	justify-content: space-around;
	margin: 15px;
	width: 100px;
	font-size: 14px;
}

.social-icon {
	margin-right: 20px;
}

/*--------------------------------------------------------------
# Archive
--------------------------------------------------------------*/
@media screen and (min-width: 1024px) {
	.archives-container {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		max-width: 1300px;
		margin: 0 auto;
	}
}

@media screen and (min-width: 1600px) {
	.archives-container {
		max-width: 1500px;
	}
}

.archive-title {
	display: flex;
	justify-content: center;
	margin: 0 auto;
	max-width: 400px;
}

.archive-title h1 {
	font-weight: bold;
	font-size: 1.3rem;
	margin-top: 1rem;
	text-transform: uppercase;
}

@media screen and (min-width: 1024px) {
	.archive-h2 {
		line-height: 2.3rem;
	}
}
